.menuoverviewIcon,
.overview {
  position: relative;
  height: 24px;
  flex-shrink: 0;
}
.menuoverviewIcon {
  width: 24px;
  overflow: hidden;
}
.overview {
  width: 156px;
  line-height: 24px;
  font-weight: 600;
  display: inline-block;
}
.typeoverviewStatusactive {
  border-radius: var(--br-9xs);
  background-color: var(--primary-color);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-xs) var(--padding-base);
  gap: var(--gap-xs);
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--light-mode-white-5-ffffff);
  font-family: var(--font-inter);
}
