.createAnAccount,
.enterYourEmail {
  margin: 0;
  position: relative;
  line-height: 150%;
  font-family: inherit;
}
.createAnAccount {
  font-size: inherit;
  letter-spacing: -0.01em;
  font-weight: 600;
}
.enterYourEmail {
  font-size: var(--font-size-base);
  font-weight: 400;
}
.copy {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-9xs);
  font-size: var(--font-size-5xl);
  color: var(--color-black);
}
.field {
  border: 0;
  background-color: transparent;
  font-family: var(--font-inter);
  font-weight: 500;
  font-size: var(--font-size-xl);
  color: var(--color-gray-100);
}
.button,
.field {
  width: 400px;
}
.inputAndButton {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.dividerChild {
  flex: 1;
  position: relative;
  background-color: var(--color-gainsboro-100);
  height: 1px;
}
.orContinueWith {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 150%;
  font-weight: 400;
  font-family: inherit;
}
.divider {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
}
.button1 {
  align-self: stretch;
  position: relative;
}
.termsOfService {
  color: var(--color-black);
}
.byClickingContinueContainer {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  line-height: 150%;
  font-weight: 400;
  font-family: inherit;
}
.content {
  position: absolute;
  top: calc(50% - 222px);
  left: calc(50% - 200px);
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xl);
  text-align: center;
  font-size: var(--font-size-base);
  color: var(--color-gray-100);
  font-family: var(--font-inter);
}
